import React, { Component } from "react";
import { Card } from "react-bootstrap";
import Statedata from "./Statedata";
import Axios from "axios";
export default class India extends Component {
  constructor() {
    super();
    this.state = {
      data: {},
    };
  }

  componentDidMount() {
    Axios.get("https://corona.lmao.ninja/v2/countries/india").then(
      (response) => {
        console.log(response.data);
        this.setState({ data: response.data });
      }
    );
  }
  render() {
    return (
      <div className="row">
        <div className="col-md-12">
          <img src="https://www.countryflags.io/in/shiny/64.png" />
          <h3>INDIA</h3>
        </div>

        <div className="col-md-12">
          <div className="row">
            <div className="col-md-3">
              <Card className="badge alert-success" style={{ width: "18rem" }}>
                <Card.Body className="text-center">
                  <Card.Title>TOTAL CASES</Card.Title>
                  <h3>{this.state.data.cases}</h3>
                  <Card.Text>[Today:{this.state.data.todayCases}]</Card.Text>
                </Card.Body>
              </Card>
            </div>

            <div className="col-md-3">
              <Card className="badge alert-primary" style={{ width: "18rem" }}>
                <Card.Body className="text-center">
                  <Card.Title>Active CASES</Card.Title>
                  <h3>{this.state.data.active}</h3>
                  <Card.Text>[Today:{this.state.data.critical}]</Card.Text>
                </Card.Body>
              </Card>
            </div>

            <div className="col-md-3">
              <Card className="badge alert-success" style={{ width: "18rem" }}>
                <Card.Body className="text-center">
                  <Card.Title>Recovered CASES</Card.Title>
                  <h3>{this.state.data.recovered}</h3>
                  <Card.Text>
                    [Today:{this.state.data.todayRecovered}]
                  </Card.Text>
                </Card.Body>
              </Card>
            </div>

            <div className="col-md-3">
              <Card className="badge alert-danger" style={{ width: "18rem" }}>
                <Card.Body className="text-center">
                  <Card.Title>DEATHS</Card.Title>
                  <h3>{this.state.data.deaths}</h3>
                  <Card.Text>[Today:{this.state.data.todayDeaths}]</Card.Text>
                </Card.Body>
              </Card>
            </div>
          </div>
        </div>

        <div className="col-md-12">
          <Statedata />
        </div>
      </div>
    );
  }
}
